<template>
  <section class="praduct">
      <div class="container">
          <div class="praduct__cards">
              <div class="praduct__card" v-for="(item, idx) in products" key="idx">
                  <img :src="item.thumbnail" alt="" class="praduct__card-img">
                  <div class="praduct__card-cost">
                      <p class="praduct__card-t">{{ item.title }}</p>
                      <p class="praduct__card-p">{{item.price}}$</p>
                  </div> 
                  <div class="praduct__card-btns">
                      <RouterLink :to="'/about/' + item.id"  class="praduct__card-buy"></RouterLink>
                      <a href="#!" class="praduct__card-add"></a>
                  </div>
              </div>
        
          </div>
      </div>
  </section>
</template>

<script>
import {  mapGetters, mapActions, mapState } from 'vuex';
import { RouterLink } from 'vue-router';
export default {
components: { RouterLink },
computed: {
...mapState(["products", "total"]),
...mapGetters(['filterProducts'])
},
methods: {
...mapActions(["getFetchproducts"]),
},
created() {
this.getFetchproducts()
},
}
</script>
