<template>
  <section class="about">
    <div class="container">
      <div class="about__cards">
        <div class="about__card">
          <div class="">
            <div class="" >
              <swiper :slides-per-view="1"        :spaceBetween="30"
              :centeredSlides="true"
              :autoplay="{
                delay: 2500,
                disableOnInteraction: false,
              }"
           
              :modules="modules"
              @autoplayTimeLeft="onAutoplayTimeLeft"
              class="mySwiper"  >
                <swiper-slide  v-for="(item, idx) in productsInfo?.images " key="idx" > 
                  <img :src="item" alt="" class="about-img">
                </swiper-slide>
                <template #container-end>
                  <div class="autoplay-progress">
                    <svg viewBox="0 0 48 48" ref="progressCircle">
                      <circle cx="24" cy="24" r="20"></circle>
                    </svg>
                    <span ref="progressContent"></span>
                  </div>
                </template>
              </swiper>
    
          </div>
          </div>
            
        </div>
        <div class="about__card">
          <div class="about__card-grade">
            <div class="about__card-grade-p">
              <Iconstar />
              <p>3.9</p>
            </div>
            <div class="about__card-grade-t">
              <p>( 23 grade )</p>
            </div>
            <div class="about__card-orders">
              <p>More than <span>400</span> orders</p>
            </div>
          </div>
          <div class="about__card-ts">
            <h2 class="about__card-category">{{ productsInfo?.category }}</h2>
            <div class="about__card-salesmam">
              <p>Salesman:</p>
              <a href="https://www.apple.com">{{ productsInfo?.brand }}</a>
            </div>
            <div class="about__card-delivery">
              <h3>Delivery: <MoreInfo /></h3>
              <p>1 day, free</p>
            </div>
            <div class="about__card-price">
              <h3>COST</h3>
              <p>{{ productsInfo?.price }}$</p>
            </div>
          </div>
          <div class="about__card-quantity">
            <p>Quantity:</p>
          </div>
          <div class="about__card-quantity-allbtns">
            <div class="about__card-quantity-btns">
              <button
                @click="changeCount('-')"
                class="about__card-quantity-btn"
              >
                -
              </button>
              <h2>{{ counter }}</h2>
              <button
                @click="changeCount('+')"
                class="about__card-quantity-btn"
              >
                +
              </button>
            </div>

            <div class="about__card-quantity-btn-p">  33</div>
          </div>
          <div class="about__card-cost">
            <p>Cost: {{ productsInfo?.price * counter }}$</p>
          </div>

          <buttonAb />
        </div>
      </div>
      <Description />
   
    </div>
  </section>
</template>

<script>
 import { ref } from 'vue';
import { mapActions, mapState } from "vuex";
import Iconstar from "@/components/icons/IconStar.vue";
import MoreInfo from "@/components/icons/MoreInfo.vue";
import buttonAb from "@/components/UI/buttons/buttonAb.vue";
import Description from "../description/description";

import { Swiper, SwiperSlide } from "swiper/vue";

import "swiper/css";
import 'swiper/css/pagination';
  import 'swiper/css/navigation';

 
  import { Autoplay, Pagination, Navigation } from 'swiper/modules';
export default {
  components: {
    Iconstar,
    MoreInfo,
    buttonAb,
    Description,
    Swiper,
    SwiperSlide,
  },
  computed: {
    ...mapState(["productsInfo", "counter"]),
    num() {
      return this.$store.state.counter;
    },
  },
  methods: {
    ...mapActions(["getSoloProduts"]),
    changeCount(sym) {
      this.$store.commit("changeCount", sym);
    },
  },
  created() {
    this.getSoloProduts(this.$route.params.id);
  },
  setup() {
      const progressCircle = ref(null);
      const progressContent = ref(null);
      const onAutoplayTimeLeft = (s, time, progress) => {
        progressCircle.value.style.setProperty('--progress', 1 - progress);
        progressContent.value.textContent = `${Math.ceil(time / 3000)}s`;
      };
      return {
        onAutoplayTimeLeft,
        progressCircle,
        progressContent,
        modules: [Autoplay, Pagination, Navigation],
      };
    },

    
};
</script>

